import React from "react"
import ReactDOM from "react-dom"
import CommentDetails, {CommentDetailsWithHooks} from "./components/CommentDetails"
import faker from "faker";
import ApprovalCard from "./components/ApprovalCard";


const App = () => {

    const datas = [
        {
            name : faker.name.firstName(),
            image : faker.image.avatar(),
            date : faker.date.past(),
            text : faker.lorem.slug(),
            isLiked : false
        },
        {
            name : faker.name.firstName(),
            image : faker.image.avatar(),
            date : faker.date.past(),
            text : faker.lorem.lines(),
            isLiked : false
        },
        {
          name : faker.name.firstName(),
          image : faker.image.avatar(),
          date : faker.date.past(),
          text : faker.lorem.sentence(),
          isLiked : false
      }
    ]
    
    return (
      <>
      <div className="ui grid padded">
      <div className="two column row">
        <div className="ui container comments eight wide column">
          <h1>Class Component </h1>
            {
                datas.map(function(item, index) {
                    return(
                        <ApprovalCard>
                            <CommentDetails key={index} image= {item.image} name = {item.name} text= {item.text} date = {formatDate(item.date)}/>
                        </ApprovalCard>
                    )
                })
            }
        </div>

<div className="ui container comments eight wide column">
  <h1>Functional Component with hooks</h1>
{
    datas.map(function(item, index) {
        return(
            <ApprovalCard>
                <CommentDetailsWithHooks key={index} image= {item.image} name = {item.name} text= {item.text} date = {formatDate(item.date)}/>
            </ApprovalCard>
        )
    })
}
</div>
</div>
</div>
</>
    )
} 

function formatDate(date) {
    var d = new Date(date),
    month = '' + (d.getMonth() + 1),
    day = '' + d.getDate(),
    year = d.getFullYear();
    if (month.length < 2) 
    month = '0' + month;
    if (day.length < 2)
    day = '0' + day;
    return [year, month, day].join('-');
 }



ReactDOM.render(<App/>, document.querySelector("#root"))

