import React, {useState} from "react"


class CommentDetails extends React.Component {
    constructor(props) {
    	super(props);
        this.state = {
            isLiked: props.isLiked
        }
    }

    handleClick() {
        console.log("I am clicked!!!!!!")
        this.setState({
               isLiked: !this.state.isLiked
         })
 }

    render() {
        return(
            <div className="ui container comments">
        <div className="comment">
          <a href="/" className="avatar"><img alt="avatar" src={this.props.image}/></a>​
          <div className="content">
             <a href="/" className="author">{this.props.name }</a>​
             <div className="metadata">​
                <span className="date"> {this.props.date} </span>​
             </div>
             <div style={{float: "right"}} onClick={()=> {this.handleClick()}}>​
                <i className={`heart red ${this.state.isLiked ? "" : "outline"} icon right`}></i>​
            </div>
        <div className="text">{this.props.text}</div>
          </div>
       </div>
    </div>
        )
    }
}


export const CommentDetailsWithHooks = (props) => {
const [isLiked, setIsLiked] = useState(props.isLiked);

    console.log(props);
    return (
        <div className="ui container comments">
    <div className="comment">
      <a href="/" className="avatar"><img alt="avatar" src={props.image}/></a>​
      <div className="content">
         <a href="/" className="author">{props.name }</a>​
         <div className="metadata">​
            <span className="date"> {props.date} </span>​
         </div>
         <div style={{float: "right"}} onClick={()=> {setIsLiked(!isLiked)}}>​
                <i className={`heart red ${isLiked ? "" : "outline"} icon right`}></i>​
            </div>
    <div className="text">{props.text}</div>
      </div>
   </div>
</div>
    )
}

export default CommentDetails;